  justify-content:space-between;
  line-height:1.36rem;
  font-size:0.36rem;
  color:#79839e;
}
.card_list{
  width:100%;
  height:7.58rem;
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom:0.49rem;
  list-style: none;
}
.card_main{
  width:2.8rem;
  height:3.58rem;
  flex-basis:2.8rem;
  flex-shrink:0;
  flex-grow:0;
  height:3.58rem;
  position:relative;
  &:nth-of-type(1),&:nth-of-type(2),&:nth-of-type(3){
    align-self:flex-start;
  }
  &:nth-of-type(4),&:nth-of-type(5),&:nth-of-type(6){
    align-self:flex-end;
  }
}
