  height:100%;
  // border:0.1rem solid #5c6cf1;
  // border-radius:0.2rem;
  // background:#f0f3ff;
  position:absolute;
  top:0;
  left:0;
  img{
    width:100%;
    height:100%;
  }
}
.card_front__link{
  width:100%;
  height:100%;
  position:absolute;
  left:0;
  top:0;
  display:none;
}
