  height:0.98rem;
  .text_oves();
}
.win_card__title,.close_card__btn{
  display:none;
}
.card_con__win{
    display:block!important;
    padding:0 0.4rem 0.5rem 0.4rem;
    position:fixed;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index:5;
    border-radius:0.28rem;
    .ovhide();
    background:#fff;
    .card_con{
      border-top:0;
    }
    .win_card__title{
      text-align:center;
      display:block;
    }
    .wps_tips{
      display:none;
    }
    .close_card__btn{
      display:block;
    }
}
.card_mask{
  width:100%;
  height:100%;
  position:fixed;
  top:0;
  left:0;
  z-index:4;
  background:rgba(0,0,0,0.5);
}
//翻牌效果实现
.card_main{
  perspective: 8rem;
  transition:opacity 0.4s;
  &.disabled{
    opacity:0.5;
  }
  &.linked{
    a{
      display:block;
    }
  }
  &.active{
    .card_font{
      transform:rotateY(0);
    }
    .card_back{
      transform:rotateY(-180deg);
    }
  }
}
.card_back{
  backface-visibility:hidden;
  transition:transform 0.8s;
  transform:rotateY(0);
}
.card_font{
  backface-visibility:hidden;
  transition:transform 0.8s;
  transform:rotateY(180deg);
}

